import { AppTalkingAio } from './appTalkingAio';
import type { AppTalkingBase } from './appTalkingBase';

export type ExitMethod = 'logout' | 'maintain' | 'accountSuspended' | 'exit';

export * from './appTalkingAio';
export * from './appTalkingBase';

export class AppTalking {
    static shared = new AppTalking();

    private appTalkingCore: AppTalkingBase = new AppTalkingAio();

    public customAppTalkingCore(appTalkingCore: AppTalkingBase) {
        this.appTalkingCore = appTalkingCore;
    }

    public onLoaded() {
        this.appTalkingCore.onLoaded();
    }

    public leaveGame(logout?: boolean) {
        this.appTalkingCore.leaveGame(logout);
    }

    public open_hall() {
        this.appTalkingCore.open_hall();
    }

    public startApp(_app: string) {
        this.appTalkingCore.startApp(_app);
    }

    public checkAppStatus(_apps: any) {
        this.appTalkingCore.checkAppStatus(_apps);
    }

    public closeWebView() {
        this.appTalkingCore.closeWebView();
    }

    public startBrowser(_root: string) {
        this.appTalkingCore.startBrowser(_root);
    }

    public closePayOnline() {
        this.appTalkingCore.closePayOnline();
    }

    public registerSuccess() {
        this.appTalkingCore.registerSuccess();
    }

    public accountSuspended() {
        this.appTalkingCore.accountSuspended();
    }

    public maintaining() {
        this.appTalkingCore.maintaining();
    }

    public notOpen() {
        this.appTalkingCore.notOpen();
    }

    public bound() {
        this.appTalkingCore.bound();
    }

    public signIn() {
        this.appTalkingCore.signIn();
    }

    public showWebGLNoSupport() {
        this.appTalkingCore.showWebGLNoSupport();
    }

    public exit(method?: ExitMethod) {
        switch (method) {
            case 'logout':
                this.leaveGame(true);
                return true;
            case 'maintain':
                this.maintaining();
                return true;
            case 'accountSuspended':
                this.accountSuspended();
                return true;
            case 'exit':
                this.leaveGame();
                return true;
            default:
                return false;
        }
    }
}